.image-container {
    display: flex;
    width: 100%;
    padding: 4% 2%;
    box-sizing: border-box;
    height: 100vh;
}

.box {
    flex: 1;
    overflow: hidden;
    transition: 1.5s;
    margin: 0 2%;
    box-shadow: 0 20px 30px rgba(0, 0, 0, .1);
    line-height: 0;
    border-radius: 10px;
}

.box>img {
    width: 100%;
    height: calc(100% - 10vh);
    object-fit: cover;
    transition: 0.5s;
}

.box>span {
    font-size: 1.2em;
    display: block;
    text-align: center;
    height: 10vh;
    line-height: 2.6;
}

.box:hover {
    flex: 1 1 50%;
}

.box:hover>img {
    object-fit: contain;
    width: 100%;
    height: 100%;
}